import React from "react";
import './systems.css'

const Systems=()=>{
    return(
        <React.Fragment>
          <div className="back-blue">
            <div className="back-white">
            <div className="items">
                <div className="items-image item1"></div>
                <div className="items-label"> سامانه بیمه تکمیلی</div>
              </div>
              <div className="items">
                <div className="items-image item2"></div>
                <div className="items-label">سامانه ورود و خروج</div>
              </div>
              <div className="items">
                <div className="items-image item3"></div>
                <div className="items-label"> سامانه فیش حقوقی</div>
              </div>
              <div className="items">
                <div className="items-image item4"></div>
                <div className="items-label"> سامانه ساپ </div>
              </div>
            </div>
          </div>
    </React.Fragment>
    );

}
export default Systems;
