import React from "react";
import "./groupset.css";

const GroupsSet=(props)=>{
    console.log(props.maneger);
    console.log("jjnnjjnjnjhjjppppppppppp")
    return(  
        
        <div className="dispgroupsetting">
            <div className="contonerinput">
               <label>نام دپارتمان یا گروه:</label>
               <label> {props.departmanname} </label>
            </div>
            <div className="contonerinput">
               <label>مدیریت دپارتمان:</label>
               <label> {props.maneger} </label>
            </div>
            <div className="contourlist">
                <div className="contourlistchild">
                    <div className="contourlistchild2 contourlistchild3">کد پرسنلی</div>
                    <div className="contourlistchild2 contourlistchild4"> نام و نام‌خانوادگی</div>
                    <div className="contourlistchild2 contourlistchild5">سمت</div>
                    <div className="contourlistchild2">دسترسی در ارسال پیام</div>
                    <div className="contourlistchild2"> دسترسی در ایجاد تیکت</div>
                </div>
                {props.Optionsvalues.map((items,index)=>{
                    return(
                    <div className="contourlistchild" >
                        <div className="contourlistchild2 contourlistchild3">{21*items.id+1245*2}</div>
                        <div className="contourlistchild2 contourlistchild4">{items.name}</div>
                        <div className="contourlistchild2 contourlistchild5">{items.position}</div>
                        <div className="contourlistchild2"><button className="contourlistchild6">تنطیم</button></div>
                        <div className="contourlistchild2"> <button className="contourlistchild6">تنطیم</button></div>
                    </div>
                    );
                })}
            </div>
        </div>
    );
};
export default GroupsSet