import React from "react";
import Select from "../select/select";
import Editor1 from "../txteditor/txteditor";
import Inputfile from "../inputfile/inputfile";
import "./reqtikets.css"
const tiketList=['درخواست مرخصی','درخواست تاسیسات','درخواست انتقالی ','درخواست وام',
'درخواست برقراری بیمه تکمیلی','درخواست لوازم اداری','گواهی اشتغال به کار','معرفی نامه به بانک']

const ReqTikets=(pops)=>{
    const names=[{name:"",}]
    return(
        <div className="Req">
            <div className="Slc"> 
                <label className="slcl">نوع درخواست:</label>
                <select className="slct">
                    {tiketList.map((items,index)=>{
                        return(
                            <option>{items}</option>
                        );
                    })}                    
                </select>
                
            </div>
            <label className="editlabel">متن درخواست:</label>
            <div className="editor">
                <Editor1>
                </Editor1>
            </div>
            <label className="editlabel">الصاق‌ها:</label>
            <div className="slcin">
                <Inputfile></Inputfile>
            </div>
            <div className="buttuncuntiner">
                <button className="btnReq"> ارسال
                </button>
            </div>

        </div>


    );
}
export default ReqTikets