const Managers=(props)=>{

const [style1,setStyle1]=useState("samane1")
const [style2,setStyle2]=useState("samane")
const [style3,setStyle3]=useState("samane")
const [style4,setStyle4]=useState("samane")
const [style5,setStyle5]=useState("grousmg-1")
const groupsName=[
  {id:1,maneger:" علی احمدی",title:"لبنیات"},
  {id:2,maneger:"ناصر صابری",title:"منابع انسانی"},
  {id:3,maneger:" علیرضا تهرانی",title:"پاندا"}
];
const [groupsNameManeger,setGroupsNameManeger]=useState(groupsName[1].maneger);
const [groupsNameTitle,setGroupsNameTitle]=useState(groupsName[1].title);







const groupClicked=(event,index)=>{
  setGroupsNameManeger(groupsName[index].maneger)
  setGroupsNameTitle(groupsName[index].title)
  
}


const ElementGr=()=>{
  return(groupsName.map((it,key)=>
    <div
     key={key} 
     className={style5} 
     onClick={(event)=>groupClicked(event,key)}>
      {it.title}
     </div>));}


const TicketManage= 
    <div className="dippart2">
    <div className="tiketsMng">
      <div className="tiketsMng-0 tiketsMng-1">شناسه</div>
      <div className="tiketsMng-0 tiketsMng-2">نام تیکت</div>
      <div className="tiketsMng-0 tiketsMng-3">تنظیمات</div>
      <div className="tiketsMng-0 tiketsMng-3" >تعداد ایجاد شده</div>
      <div className="tiketsMng-0 ">وضعیت</div>
    </div>
    {
      tiketsCreated.map((items)=>{
        return(
          <div className="tiketsMng">
            <div className="tiketsMng-0 tiketsMng-1">{items.id}</div>
            <div className="tiketsMng-0 tiketsMng-2">{items.name}</div>
            <button className="tiketsMng-0 tiketsMng-4">ویرایش</button>
            <div className="tiketsMng-0 " >{items.num}</div>
            <div className="tiketsMng-0 tiketsMng-5">{items.condition}</div>
          </div>
        );
      })
    }
    </div>;

const TicketCreate=
      <div className="dippart2">
        <div className="checkboxing8">
          <span>نام تیکت:</span>
          <input className="checkboxing9"></input>
        </div>
        <div className="checkboxing10">
          <Select resivers="ایجاد کنندگان"> </Select>
        </div>
        <div className="checkboxing2" >
          <input type="checkbox" className="checkboxing" ></input> <span> بستن خودکار تیکت ها بعد از  اتمام مرحل</span>
        </div>
        <div className="checkboxing1">
            <input type="checkbox" className="checkboxing" ></input>  <span> امکان مشاهد وضعیت تیکت توسط ایجاد کننده</span>
        </div>
        <div className="checkboxing1">
          <input type="checkbox" className="checkboxing" ></input>  <span> امکان مشاهده وضعیت تیکت توسط تایید کنندگان</span>
        </div>
        <div className="checkboxing1">
          <input type="checkbox" className="checkboxing" ></input>  <span> نمایش لینک ثبت تیکت در پورتال</span>
        </div>
        <div className="checkboxing1"> 
          <input type="checkbox" className="checkboxing" ></input>  <span> عدم ارسال ایمیل در صورت درج پاسخ به تیکت</span>
        </div>
        <div className="checkboxing3">
          <span>فرم ثبت ایمیل از طریق پرتال یا ایمیل</span> 
          <select className="checkboxing4" >
          <option>از طریق پرتال </option>
            <option>از طریق ایمیل </option>
          </select>
        </div>
        <div>
          
          <button className="checkboxing5">ویرایش مسیر تیکت</button>
        </div>
        <div className="checkboxing7"> 
          <button className="checkboxing6"> ایجاد کن </button>
        </div>
   </div>;

   const DepartManegment=
  <div className="dippart2">
    <div className="dippart3">
      <div className="groupsMng">
         <ElementGr></ElementGr>
      </div>
      <GroupsSet  Optionsvalues={Optionsvalue} departmanname={groupsNameTitle}
       maneger={groupsNameManeger}/* okClicked={okClickHandeler}*/></GroupsSet>
      </div>
  </div>;


const [Mining,setMining]=useState(TicketManage);
 
const clickedHandeler=(event,key)=>{
      event.preventDefault()
      switch(key){
        case 1:
          setStyle1("samane1");
          setStyle2("samane");
          setStyle3("samane");
          setStyle4("samane");
          setMining(TicketManage);
          break;
        case 2:
          setStyle1("samane");
          setStyle2("samane1");
          setStyle3("samane");
          setStyle4("samane");
          setMining(TicketCreate);
          break;
        case 3:
          setStyle1("samane");
          setStyle2("samane");
          setStyle3("samane1");
          setStyle4("samane");
          setMining( DepartManegment);
          break;
        case 4:
          setStyle1("samane");
          setStyle2("samane");
          setStyle3("samane");
          setStyle4("samane1");
          setMining(TicketManage);
          break;
          default:
            break;
      }
     }


 return(
        <React.Fragment>
        <div  className="dippart">
            <div className="dippart1">
              <div className={style1} onClick={(event)=>{clickedHandeler(event,1)}}>مدیریت تیکت‌ها</div>
              <div className={style2} onClick={(event)=>{clickedHandeler(event,2)}}>ایجاد تیکت جدید</div>
              <div className={style3} onClick={(event)=>{clickedHandeler(event,3)}}>مدیریت دپارتمان ها</div>
              <div className={style4} onClick={(event)=>{clickedHandeler(event,4)}} >ایجاد دپارتمان جدید</div>
          </div>
          {Mining}          
        </div>

      </React.Fragment>
       
    );
}
export default Managers